.launchDetails {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	&__navigation {
		background: #000;
		display: flex;
		color: #ccac5b;
		height: 100px;
		align-items: center; }
	&__arrow {
		text-transform: uppercase;
		letter-spacing: 8px;
		font-size: 0.8rem;
		flex-grow: 1;
		padding-left: 2rem; }
	&__logo {
		flex-grow: 2;
		padding-left: 2rem;
		img {
			width: 300px; } } }

.launchInfo {
	padding: 4rem 2rem;
	display: flex;
	.grid__half {
		flex-grow: 1;
		width: 50%; }
	.rocket__dateLaunch {
		text-transform: uppercase;
		letter-spacing: 8px;
		color: #a7a9ac;
		margin-bottom: 2rem; }
	.rocket__name {
		text-transform: uppercase;
		font-size: 3.5rem; }
	.rocket__counter {
		text-transform: uppercase;
		margin-bottom: 2rem; }
	.rocket__container {
		width: 100%;
		display: flex;
		letter-spacing: 2px;
		text-transform: uppercase;
		font-size: 1rem;
		.rocket__column {
			display: flex;
			flex-direction: column;
			justify-content: space-around; }
		.rocket__item {
			line-height: 1.75rem;
			display: flex; }
		.rocket__key {
			color: #a7a9ac;
			margin-right: 8px;
			align-self: flex-start; } }

	.rocket__title {
		width: 100%; }
	.rocket__column {
		width: 50%;
		padding-left: 2em;
		border-left: 1px grey dashed;
		margin-top: 2em; }

	h4 {
		color: #ccac5b;
		text-transform: uppercase;
		letter-spacing: 8px;
		font-weight: 100; }
	hr {
		border: 0;
		border-top: 2px solid #ccac5b; } }

.mission {
	box-sizing: border-box;
	background-image: url('../assets/rocket.png');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	width: 100%;
	overflow: hidden;
	min-height: 700px;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: flex-end;
	flex-wrap: wrap;
	color: #fff;
	&__title {
		text-align: center;
		color: #fff;
		width: 100%;
		font-size: 2rem;
		text-transform: uppercase;
		letter-spacing: 25px;
		font-weight: 200; }
	&__links {
		background-color: transparent;
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		flex-direction: row;
		margin-bottom: 1rem;
		a {
			color: #fff;
			text-transform: uppercase;
			letter-spacing: 5px;
			margin: 0 1rem;
			padding: 1rem 2rem;
			display: block;
			text-decoration: none;
			font-size: 1rem;
			border: 1px solid #ffffff;
			border-radius: 2em; } } }

